import {home} from '~/apis'
import snow from '~/static/js/snow2'
import {isMobile} from "~/utils"
import moment from 'moment'

let sliding = false
let scrollTop = 0

export default {
  data () {
    return {
      tabs: [{
        text: '前端',
        url: '/front-end'
      }, {
        text: '游记',
        url: '/travel'
        // }, {
        //   text: '生活',
        //   url: '/essay'
      }],
      showContent: false,
      goUp: false,
      bgSlide: false,
      contactType: 'wechat'
    }
  },
  head () {
    return {
      title: '康哥的生活分享',
      meta: [
        {hid: 'keywords', name: 'keywords', content: '技术分享,户外游记,生活随笔'}
      ]
    }
  },
  methods: {
    handleSlide () {
      if (!this.showContent) {
        this.showContent = true
        this.goUp = true
        this.bgSlide = true
        this.$nextTick(() => {
          this.mapInit()
        })
        sliding = true
        setTimeout(() => {
          sliding = false
        }, 400)
      } else {

        this.goUp = false
        this.bgSlide = false
        sliding = true
        setTimeout(() => {
          this.showContent = false
          sliding = false
        }, 400)
        // let node = document.querySelector ('#content')
        // let callBack = () => {
        //   console.log ('触发')
        //   this.showContent = false
        //   node.removeEventListener ('webkitAnimationEnd', callBack)
        // }
        // node.addEventListener ('webkitAnimationEnd', callBack)
      }
    },
    slideInit () {
      let windowHeight = document.documentElement.clientHeight || document.body.clientHeight
      let contentNodeHeight = document.getElementById("index-bg").offsetHeight
      document.onscroll = (e) => {
        let scrollHeight = document.documentElement.scrollTop || document.body.scrollTop
        if (scrollHeight + windowHeight >= contentNodeHeight && !this.showContent && scrollHeight - scrollTop > 0 && !sliding) {
          this.handleSlide()
        } else if (scrollHeight + windowHeight < contentNodeHeight && this.showContent && scrollHeight - scrollTop < 0 && !sliding) {
          this.handleSlide()
        }
        scrollTop = scrollHeight
      }
    },
    mapInit () {
      var myChart = echarts.init(document.getElementById('map'));
      home.queryFootprints().then(({data}) => {
        console.log(data)
        var option = {
          tooltip: {
            trigger: 'item',
            formatter ({data}) {
              return `${data.name} <br/> ${moment(data.date).format('YYYY.MM.DD')}`
            },
          },
          geo: {
            map: 'china',
            label: {
              emphasis: {
                show: false
              }
            },
            roam: true,
            scaleLimit: {
              min: 1,
              max: 3
            },
            itemStyle: {
              normal: {
                areaColor: '#ccc',
                borderColor: '#888'
              },
              emphasis: {
                areaColor: '#ccc'
              }
            }
          },
          series: [{
            name: '地址',
            type: 'scatter',
            coordinateSystem: 'geo',
            data: data,
            symbolSize: 8,
          }]
        }
        console.log(option)
        myChart.setOption(option)
      }).catch(err => {console.log(err)})
    }
  },
  layout: 'home',
  asyncData () {
  },
  mounted () {
    if (!isMobile()) {
      snow()
    }
    this.slideInit()
  }
}
